  --bg-hero: #ffb8ca;
  --bg-circle: #ffa5ba;
  --bg-second: #fff5f6;
  --bg-comment: #fff5f6;
  --bg-menu: #e17992;
  --bg-menu-content: #e18298;
  --btn-hover: #ecebeb;
  --btn-hover-accent: #c7133e;
  --timing-function: cubic-bezier(.4, 0, .2, 1);
}

