.gallery {
  background: linear-gradient(to bottom, var(--bg-second) 10%, var(--primari-white-color) 90%);
}

.gallery__list {
  grid-template-rows: repeat(21, minmax(8px, 1fr));
  grid-template-columns: repeat(7, minmax(41px, 1fr));
  gap: 2px;
  display: grid;
}

@media screen and (min-width: 768px) {
  .gallery__list {
    grid-template-rows: repeat(21, minmax(12px, 1fr));
    grid-template-columns: repeat(7, minmax(90px, 1fr));
    gap: 4px;
  }
}

